.NumiGhost {
  flex-shrink: 0;
  min-width: 244px;
  flex-grow: 1;
  aspect-ratio: .691;
}

.border {
  border: 2px dashed #f2f2f2;
  border-radius: 10px;
  margin-top: 16px;
  padding: 20px;
  width: calc(100%-40px);
}

.image {
  margin-top: -36px;
  aspect-ratio: 1;
}

.picture {
  border-radius: 50%;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.timer {
  padding: 5px 8px;
  width: 100px;
  padding: 10px;
  margin-top: -18px;
  margin-left: 3px;
  border-radius: 3px;
}

.field {
  margin-top: 12px;
  margin-right: 30px;
  padding: 10px;
}

.spacer {
  border: 1px solid #f2f2f2;
  border-radius: 50%;
  margin: 12px 20px;
}

.picture, .timer, .field {
  background: linear-gradient(
    to right, 
    #ffffff 0%,
    #f3f3f3 49%,
    #ededed 50%,
    #ffffff 100%
  );
  background-size: 200% 200%;
  background-repeat: no-repeat;
}

.picture, .timer, .field {
  animation: ghost-loading 2s ease-in-out infinite;
}
/* 
.border {
  animation: ghost-loading 3s ease-in-out infinite;
} */

@keyframes ghost-loading {   
  0% {     
    background-position: 200% 0%;
  }   
  50% {     
    background-position: 200% 0%;
  }   
  100% {
    background-position: -100% 0%;
    
  }
}
