.filterAndSort {
  position: relative;  
  z-index: 999;
}

.filterAndSortMenu {
  min-width: 180px;
  width: max-content;
  padding: 20px;
  position: absolute;
  right: 0;
  border: 1px solid var(--color-active);
  border-radius: 10px;
  background-color: white;
}

.filterAndSortMenu a {
  justify-content: flex-start;
}

.title {
  font-weight: 600;
  color: var(--color-active);
  margin-bottom: 8px;
}

.spacer {
  border: 1px solid #f2f2f2;
  border-radius: 50%;
  margin: 12px 20px;
}
